@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

* {
}
div .navigation {
  min-height: 100vh;
  background: #150019;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-right: 10px;
}

div ul.sidebar-ul li.sidebar-li {
  font-family: "Poppins", sans-serif;
  position: relative;
  width: 100%;
  list-style: none;
}

div ul.sidebar-ul li.sidebar-li:hover {
  background: #ea1d63;
}

.navigation {
  position: fixed;
  top: 60px;
  width: 60px;
  height: 100px;
  background-color: #3e0748;
  transition: 0.5s;
  overflow: hidden;
  z-index: 2;
}

.navigation:hover,
.navigation.active {
  width: 300px;
}

.navigation ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 0;
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  display: flex;
  text-decoration: none;
  color: #fff;
}

.navigation ul li a .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
}

.navigation ul li a .icon .fa {
  font-size: 24px;
}

.navigation ul li a .title {
  position: relative;
  display: block;
  padding: 0 10px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
}

.toggle {
  position: fixed;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background: #330748;
  cursor: pointer;
  z-index: 3;
}

.toggle.active {
  background: #ea1d63;
}

.toggle:before {
  content: "\f0c9";
  font-family: fontAwesome;
  position: absolute;
  width: 100%;
  height: 100%;
  line-height: 60px;
  text-align: center;
  font-size: 24px;
  color: #fff;
}

.toggle.active:before {
  content: "\f00d";
}

@media (max-width: 767px) {
  .navigation {
    left: -60px;
  }

  .navigation.active {
    left: 0px;
    width: 100%;
  }
}
